<template>
    <h2>Integrate</h2>
</template>
<script>
import DataService from '../../services/DataService';
export default {
   created:function(){
    var vm=this;
    vm.GetAccessToken();
   },
   methods:{
    GetAccessToken:function(){
        //debugger;
        var vm=this;
        var params=`code=${vm.$route.query.code}`;
        DataService.GetAccessTokenWithPKCE(params).then((response) => {
            var driveId=response.data;
            this.ImportOneDriveFile(driveId);
            if(vm.$route.query.code!=null){
                vm.$router.push({ name: "AdditionalStorage", params: {type:'onedrive' } });
            }
        })
    },
    ImportOneDriveFile: async function(id) {
        var vm = this;
        var params = `additionalStorageId=${id}`;
        try {
            await DataService.ImportOneDriveFile(params).then((response) => {
                //debugger
            });
            // Any code that needs to be executed after the import is complete can go here
        } catch (error) {
            // Handle errors if any
            console.error("Error importing OneDrive file:", error);
        }
    }
   }
}
</script>
